  border: 2px solid #00A9A5;
  margin-left: 3px;
  margin-right: 3px;
}

.setterButton:focus {
  outline: none;
}

.arrow-down {
  width: 0; 
  height: 0; 
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #00A9A5;
}

.arrow-up {
  width: 0; 
  height: 0; 
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #00A9A5;
}



@media (max-width: 500px) {
  .App {
    height: 22em;
    width: 22em;
  }
  .mainDisplayAnchor {
    font-size: 3em;
    top: 10px;
  }
}


